.fake-buttons {
  width: 45px;
  position: absolute;
  top: 7px;
  left: 7px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.fake-buttons div {
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    width: 10px;
    height: 10px;
}
.fake-buttons div.close {
      background-color: #FF4558;
}
.fake-buttons div.minimize {
      background-color: #FFB955;
}
.fake-buttons div.fullscreen {
      background-color: #24CA5B;
}
.value-wrapper {
  display: -webkit-box;
