  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  padding: 22px 15px 5px 15px;
  position: relative;
}
.buttons {
  display: grid;
  grid-template-rows: repeat(5, 47px);
  grid-template-columns: repeat(4, 58px);
  grid-gap: 1px;
}
.buttons div {
    cursor: pointer;
}
.advanced-wrapper {
  grid-column: 1 / 4;
}
.basic-wrapper {
  grid-column: 4 / 5;
  grid-row: 1 / 6;
  color: white;
}
.numbers-wrapper {
  grid-row: 2 / 5;
}
.numbers {
  display: grid;
  grid-template-rows: repeat(4, 47px);
  grid-template-columns: repeat(3, 58px);
  grid-gap: 1px;
}
.number[data-number="0"] {
  grid-column: 1 / 3;
}
.number.clicked div {
  background-color: #d5d5d5;
}

